import { Project } from "../../../domain/domain";
import { DataTableProps } from "../../../typings";
import HeaderRow from "../../commons/table/HeaderRow.component";
import './project-table.scss';

const ProjectDataTable = ({ data, meta }: DataTableProps<Project>) => {
    const renderData = (d: any) => {
        if (typeof d === 'string') {
            return d;
        }
        return null;
    }
    return data?.length > 0 ? (
        <div style={{ maxWidth: '55vw', overflow: 'auto', marginLeft: 15, maxHeight: '20vh', overflowY: 'auto' }}>
          <table className="project-table" style={{ borderCollapse: 'collapse', borderRadius: 5 }}>
            <thead>
              <HeaderRow>
                {
                  Object.entries(meta).map(([k, d]) => <th key={k}>{d.displayName ?? k}</th>)
                }
              </HeaderRow>
            </thead>
            <tbody>
                {
                    data.map(dt => (
                        <tr>
                            {
                                Object.entries(meta).map(([k, d]) => <td key={k}>{d.render ? d.render(dt) : renderData(dt[k as keyof Project]) }</td>)
                            }
                        </tr>
                    ))
                }
            </tbody>
          </table>
        </div> 
    ) : null;
};

export default ProjectDataTable;
